@font-face {
   font-family: SF Pro Display;
   src: url('../assets/fonts/SFProDisplay-Regular.ttf');
}

body {
   height: 100%;
   overflow: hidden;
   position:fixed;
}

* {
   font-family: 'SF Pro Display Semibold', 'SF Pro Display';
}

h1 {
   font-family: 'SF Pro Display Semibold', 'SF Pro Display';
   font-weight: 600;
   font-style: normal;
   font-size: 20px;
}

h2 {
   font-family: 'SF Pro Display';
   font-weight: 400;
   font-style: normal;
   font-size: 16px;
}

p {
   font-family: 'SF Pro Display';
   font-weight: 400;
   font-style: normal;
   font-size: 14px;
   color: rgba(0, 0, 0, 0.65);
}

.preloader {
   display: flex;
   height: 100vh;
   justify-content: center;
   align-items: center;
}

.ant-btn-secondary {
   height: 60px;
   width: 250px;
   margin: 5px 0px;
   color: #1890ff;
   border: 1px solid #1890ff;
   line-height: 60px !important;
   &:hover {
      background: #1890ff;
      color: #fff;
   }
}

.auth__type {
   width: 430px;
   border-radius: 10px;
   box-shadow: 0px 0px 30px rgba(24, 144, 255, 0.28);
   padding: 30px 40px 30px 40px;

   .ant-btn-secondary {
      width: 350px;
   }

   .auth__type_server {
      // position: absolute;
      // width: 100%;
      // left: 0;
      // border-top: 1px solid #eee;
      // margin-top: 15px;
      display: flex;
      justify-content: flex-start;
      color: #b5b5b5;
      align-items: center;
      padding: 24px 0;

      span {
         padding-right: 10px;
      }

      .ant-dropdown-link {
         color: #1890ff;
      }

      .ant-select-selection--single {
         display: flex;
         height: auto;
      }

      .ant-select-selection-selected-value {
         display: flex !important;
         align-items: center;
         
         .node_fields {
            padding: 10px;
            line-height: 17px;
      
            .node_field {
               color: #b5b5b5;
            }
         }
      }
   }

   h1, h2 {
      text-align: center;
   }
}

.ant-select-dropdown-menu-item {
   display: flex;
   padding: 10px 12px;
   
   .node_fields {
      padding-left: 10px;
      line-height: 17px;

      .node_field {
         color: #b5b5b5;
      }
   }
}

.reg__form {
   padding: 30px 40px 30px 40px;
}

.page {
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.page-top {
   display: flex;
}

.page__content {
   display: flex;
   height: 100%;
}

.dialog-container {
   width: 100%;
   height: 100%;

   .dialog {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: calc(100% - 64px);
      margin-left: -1px;
      background: #f9f9f9;
      border-left: 1px solid #e4e4e4;
      border-bottom: 1px solid #e4e4e4;
   
      .dialog__messages-viewport {
         // height: 100%;
         flex: 1;
         overflow-y: auto;
         overflow-x: hidden;
   
         .dialog__messages {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 20px;
            min-height: 100%;
         }
   
         .message {
            margin: 10px 0px;

            .wrap-message-content {
               display: flex;
               max-width: 80%;

               .avatar {
                  align-self: flex-end;
                  flex-shrink: 0;
                  width: 50px;
                  height: 50px;
                  border-radius: 100%;
                  margin-right: 10px;
                  line-height: 50px;
                  overflow: hidden;
               }
      
               .message__img {
                  max-width: 150px;
                  // height: 100px;
                  overflow: hidden;
                  cursor: pointer;
      
                  img {
                     height: 100%;
                     width: 100%;
                  }
               }
   
               .download-icon {
                  padding-right: 15px;
               }

               .message__read {
                  display: flex;
                  align-self: flex-end;
                  flex-shrink: 0;
                  width: 15px;
                  height: 15px;
                  fill: #1890ff;
                  margin-left: 5px;
               }
            }
   
            .message-menu {
               ul {
                  margin-right: 15px;
                  margin-bottom: 0;
                  list-style: none;
                  display: flex;
                  
                  li {
                     padding: 5px;
                     cursor: pointer;
   
                     &:hover {
                        text-decoration: underline;
                     }
                  }
               }
            }
         }
   
         .message-in {
            .message__content {
               word-break: break-word;
               flex-shrink: 0;
               max-width: 100%;
               font-size: 16px;
               background: #e4e4e4;
               padding: 10px;
               border-radius: 10px 10px 10px 0px;
   
               .message__name {
                  font-weight: 700;
                  font-size: 14px;
               }

               a {
                  text-decoration: underline;
               }

               .forward-message-text {
                  padding-left: 15px;
                  margin: 5px 0px;
                  border-left: 2px solid #000000a6;
               }
            }

            .key_message {
               background: #3c3c3c;
               color: #fff;

               .message__name {
                  color: #fff;
               }
            }

            .encrypted_message {
               background: #737373;
               color: #fff;

               .message__name {
                  color: #fff;
               }
            }
   
            .message__img {
               border-radius: 10px 10px 10px 0px;
            }
   
            .message__time {
               color: #717171;
               font-size: 14px;
               margin: 0px 0px 10px 10px;
            }

            .message-menu {
               ul {
                  margin-right: 0;
                  margin-left: 10px;
               }
            }
         }
   
         .message-out {
            // display: flex;
            // flex-direction: row-reverse;
            // align-self: flex-end;
            // max-width: 80%;
            width: 100%;

            .wrap-message-content {
               display: flex;
               flex-direction: row-reverse;
               margin-left: auto;

               .message__content {
                  word-break: break-word;
                  flex-shrink: 0;
                  max-width: 100%;
                  font-size: 16px;
                  background: #1890ff;
                  padding: 10px;
                  color: #fff;
                  border-radius: 10px 10px 0px 10px;
   
                  a {
                     color: #fff;
                     text-decoration: underline;
                  }

                  .forward-message-text {
                     padding-left: 15px;
                     margin: 5px 0px;
                     border-left: 2px solid #fff;
                  }
               }

               .key_message {
                  background: #3c3c3c;
                  color: #fff;
               }

               .encrypted_message {
                  background: #737373;
                  color: #fff;
   
                  .message__name {
                     color: #fff;
                  }
               }
      
               .message__img {
                  border-radius: 10px 10px 0px 10px;
               }
      
               .message__time {
                  color: #717171;
                  font-size: 14px;
                  margin: 10px 10px 10px 0px;
               }
            }

            .message-menu {
               display: flex;
               justify-content: flex-end;
            }
         }
      }

      .file-over {
         background: #e2f1ff;
         transition: .5s;
      }
   
      .new-message {
         display: flex;
         height: 64px;
         align-items: center;
         border-top: 1px solid #eee;
         padding: 10px 20px;
   
         .new-message__attach {
            width: 15px;
            height: 30px;
            fill: #1890ff;
            cursor: pointer;
            transition: .5s;
            &:hover {
               opacity: .8;
            }
         }
   
         .new-message__inp {
            width: 100%;
            border: 1px solid #eee;
            border-radius: 100px;
            padding: 10px;
            outline: none;
            margin: 0px 20px;
         }
   
         .new-message__send {
            width: 30px;
            height: 30px;
            fill: #1890ff;
            cursor: pointer;
            transition: .5s;
            &:hover {
               opacity: .8;
            }
         }
      }
   
      .reply-message {
         margin-left: 60px;
      }
   }
}

.drawer-user {
   display: flex;
   align-items: center;
   border-bottom: 1px solid #e4e4e4;
   padding-bottom: 10px;

   .drawer-user__ava {
      width: 60px;
      height: 60px;
      font-size: 28px;
      line-height: 60px;
      border-radius: 100px;
      overflow: hidden;

      .avatar-default {
         line-height: 60px;
      }
   }

   .drawer-user__name {
      font-size: 20px;
      padding-left: 10px;
   }
}

.avatar-default {
   width: 100%;
   height: 100%;
   text-align: center;
   line-height: 50px;
   border-radius: 100%;
   font-weight: 400;
   font-size: 23px;
   color: #1890FF;
   background: #e4e4e4;
}

.switch-label {
   display: flex;
   justify-content: space-between;

   button {
      margin-left: 10px;
   }
}

.modal_back {
   position: absolute;
   top: 20px;
   left: 20px;
   padding: 10px;
   cursor: pointer;
}

.contacts {
   .ant-modal-body {
      padding: 0;
   }

   .ant-modal-header {
      padding-left: 40px;
   }

   .content {
      padding: 15px;
      border-top: 1px solid #e4e4e4;

      .modal_back {
         position: absolute;
         top: 7px;
         left: 5px;
      }
      
   }

   .contact {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      cursor: pointer;
      &:after {
         content: '';
         position: absolute;
         width: 93%;
         height: 1px;
         background: #e4e4e4;
         bottom: 0;
         left: 0;
         right: 0;
         margin: auto;
      }
      &:last-child {
         border: none;
      }
      &:hover {
         background: #f9f9f9;
      }

      .avatar {
         flex-shrink: 0;
         width: 50px;
         height: 50px;
      }

      .name {
         width: 100%;
         padding: 15px 0;
         font-size: 16px;
         padding-left: 10px;
         font-weight: 700;
      }
   }

   .settings {
      .avatar {
         width: 120px;
         height: 120px;
         margin: 0 auto;
      }

      .ant-btn-link {
         padding-left: 0;
      }
   }
}

.ant-upload-picture-card-wrapper {
   display: table;
   margin: 0 auto;
   width: auto;

   .ant-upload-select-picture-card {
      display: block;
      width: 140px;
      height: 140px;
      border-radius: 100%;

      .ant-upload {
         position: relative;
         display: block;
         width: 120px;
         height: 120px;
         overflow: hidden;
         border-radius: 100%;
         padding: 0;
         margin: 9px;

         .loading-icon {
            position: absolute;
            left: 0;
            right: 0;
            margin:  auto;

            .anticon-spin {
               width: 40px;
               height: 40px;
               margin-top: 40px;
            }
         }
      }
   }
}

.ant-drawer-body {
   .menu {
      list-style: none;
      font-size: 16px;
      padding-left: 0;
      padding-top: 10px;

      li {
         padding: 10px 0;
         cursor: pointer;
         -webkit-transition: 0.5s;
         -moz-transition: 0.5s;
         -o-transition: 0.5s;
         transition: 0.5s;
         &:hover {
            color: #1890ff;
         }
      }
   }
}

.head-dialog {
   display: flex;
   flex-grow: 1;
   align-items: center;
   height: 70px;
   border-bottom: 1px solid #e4e4e4;

   .switch-label {
      margin-left: auto;
      margin-right: 10px;
   }

   .head-dialog__back {
      padding: 0px 25px;
      cursor: pointer;
   }

   .head-dialog__user {
      display: flex;
      align-items: center;
      max-width: 350px;
      padding: 10px;
      cursor: pointer;

      .head-dialog__ava {
         width: 50px;
         height: 50px;
         border-radius: 100%;
         overflow: hidden;
         margin-right: 15px;
         flex-shrink: 0;

         .avatar {
            width: 100%;
            height: 100%;
         }

         img {
            height: 100%;
         }
      }

      .head-dialog__photo-default {
         line-height: 50px;
         font-size: 23px;
      }

      .head-dialog__name {
         font-weight: 700;
         font-style: normal;
         font-size: 16px;
      }

      .head-dialog__status {
         font-weight: 400;
         font-size: 14px;
         color: rgba(0, 0, 0, 0.65);
      }
   }

   .head-dialog__lock {
      margin-left: auto;
      margin-right: 20px;
      svg {
         fill: #1890ff;
         width: 30px;
         height: 30px;
      }
   }

   .head-dialog__show_sidebar {
      display: none;
      cursor: pointer;
   }
}

.sidebar {
   min-width: 350px;
   flex-shrink: 0;
   z-index: 1;

   a {
      color: inherit;
   }

   .sidebar-viewport {
      height: calc(100vh - 102px);
      overflow-y: auto;
      border-right: 1px solid #e4e4e4;

      .sidebar__item {
         position: relative;
         display: flex;
         max-width: 350px;
         padding: 15px 10px;
         border-bottom: 1px solid #e4e4e4;
         cursor: pointer;
         transition: .5s;
         &:hover {
            background: #f9f9f9;
         }
   
         .sidebar__ava {
            width: 60px;
            height: 60px;
            margin-right: 15px;
            flex-shrink: 0;
   
            .sidebar__photo {
               height: 100%;
               border-radius: 100%;
               overflow: hidden;
   
               img {
                  height: 100%;
               }
   
               .sidebar__photo-default {
                  line-height: 60px;
                  font-size: 28px;
               }
            }
   
            .sidebar__badge {
               position: absolute;
               width: 20px;
               height: 20px;
               background: #1890ff;
               border-radius: 100%;
               color: #fff;
               text-align: center;
               font-weight: 400;
               font-size: 12px
            }
         }
   
         .sidebar__name {
            display: flex;
            font-weight: 700;
            font-style: normal;
            font-size: 16px;
   
            span {
               max-width: 240px;
               white-space: nowrap;
               overflow: hidden;
               text-overflow: ellipsis;
            }
   
            // &:after {
            //    content: '';
            //    display: block;
            //    width: 8px;
            //    height: 8px;
            //    background: #1890ff;
            //    border-radius: 100%;
            //    margin-top: 8px;
            //    margin-left: 5px;
            // }
         }
   
         .sidebar__message {
            word-break: break-word;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: rgba(0, 0, 0, 0.65);
         }
      }

      .sidebar__title {
         font-size: 16px;
         font-weight: 700;
         padding: 20px 0 0 10px;
         border-bottom: 1px solid #e4e4e4;
      }
   }

   .head-sidebar {
      display: flex;
      align-items: center;
      height: 70px;
      border-bottom: 1px solid #e4e4e4;
      border-right: 1px solid #e4e4e4;
      background: #fff;

      .menu-btn {
         margin-left: 10px;
      }
   
      .head-sidebar__logo {
         padding-left: 25px;
   
         img {
            width: 139px;
            height: 49px;
         }
      }
   }

   .search {
      .ant-input {
         border: none;
         border-bottom: 1px solid #e4e4e4;
         border-radius: 0;

         &:focus {
            box-shadow: none;
         }
      }

      .ant-input-search-button {
         border-radius: 0;
      }
   }

   .socket_reconnect {
      position: fixed;
      bottom: 0px;
      z-index: 10;
      background: #fff;
      padding: 21px;
      border-top: 1px solid #e4e4e4;

      .ant-spin {
         padding-right: 10px;
      }
   }
}

.popup__img {
   width: 100%;
}

.ant-modal-close {
   top: 15px;
   right: 10px;
}

.ant-modal-close-x {
   width: 26px;
   height: 26px;
   line-height: 26px;
}

.profile {
   .avatar {
      width: 100px;
      height: 100px;
      margin: 0 auto;

      .avatar-default {
         font-size: 45px;
         line-height: 100px;
      }
   }

   .profile__name {
      text-align: center;
      font-size: 28px;
      font-weight: 700;
      margin-top: 20px;
      padding-bottom: 15px;
   }

   .profile__info {
      display: flex;
      font-size: 14px;
      color: #000000a5;
      padding: 5px 0px;

      .profile__info_name {
         width: 100px;
      }

      .profile__info_users {
         display: flex;
         flex-wrap: wrap;

         .profile__info_user {
            width: 130px;
            text-align: center;
            padding: 0px 10px 20px 10px;
            cursor: pointer;

            .profile__info_ava {
               width: 50px;
               height: 50px;
               border-radius: 100px;
               text-align: center;
               line-height: 50px;
               font-weight: 400;
               font-size: 23px;
               color: #1890FF;
               background: #e4e4e4;
               margin: 0 auto;
               margin-top: 10px;
            }
         }
      }
   }

   .profile__open_chat {
      margin-top: 30px;
      cursor: pointer;
      color: #1890ff;
      &:hover {
         text-decoration: underline;
      }
   }
}

.create-chat-modal {
   .wrap_contacts {
      .contacts {
         display: flex;
         align-items: center;
         padding: 10px;
         cursor: pointer;
         &:hover {
            background: #ececec;
         }
         
         .avatar {
            width: 50px;
            height: 50px;
            margin-right: 10px;
         }

         .remove-user {
            margin-left: auto;
         }
      }

      .contacts-active {
         background: #deefff !important;
      }
   }

   .ant-modal-header {
      padding-left: 40px;
   }

   .modal_back {
      position: absolute;
      top: 7px;
      left: 5px;
   }
}

.app-version {
   position: absolute;
   bottom: 0;
}

.login-form {
   .verify-text {
      text-align: center;
      padding-bottom: 15px;

      span {
         display: block;
         margin-top: -10px;
      }
   }
}


@media screen and (max-width: 675px) {
   .sidebar {
      width: 100%;

      .sidebar-viewport {
         .sidebar__item {
            max-width: 100%;
            width: 100%;
         }
      }
   }

   .head-dialog {
      .head-dialog__user {
         .head-dialog__name {
            line-height: 17px;
         }
      }
   }

   .dialog-container {
      position: absolute;
      top: 0;
      left: 0;
      background: #fff;
   }

   .auth__type {
      width: 320px;

      .ant-btn-secondary {
         width: 240px;
      }

      .auth__type_server {
         .ant-select-selection-selected-value {
            .node_fields {
               padding: 10px 0px;
               line-height: 17px;
            }
   
            .node_photo {
               display: none;
            }
         }
      }
   }
}